body {
  height: 100vh; /* Set body height to 100% of the viewport height */
  margin: 0; /* Remove default margin */
  padding: 0; /* Remove default padding */
  background-image: url('https://as1.ftcdn.net/v2/jpg/06/87/07/86/1000_F_687078665_TSyxyPmj5HD4yTZCSuN7NGLLMC3XD1SK.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  font-family: Arial, sans-serif; /* Choose an appropriate font */
}
.nav-div{
  /* background-color: #200E3A; */
  background-color: #E36414;
}
.nav-ul li{
  display : inline-block;
  padding : 10px;
}


.nav-ul li a{
  color: white;
  padding: 5px;
  text-decoration: none;
}
.nav-ul {
  padding: 5px;
  margin: 0;
  /* background-color: skyblue; */
}
.inputBox{
  display: block;
  margin-left: 620px;
  margin-bottom: 25px;
  padding: 7px;
  width: 300px;
  border: 1px solid skyblue;;

}
.appButton{
  margin-right: 200px;
  padding: 5px;
  width: 100px;
  background-color: skyblue;
  border: 1px solid;
  cursor: pointer;
}
.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px;
  /* background: #6cf; */
  /* background-color: #200E3A; */
  background-color: #E36414;
  /* padding: 0; */
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.nav-right{
  text-align: right;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.nav-img{
  width: 70px;
  float: left;
  border-radius: 50%; 
  /* margin-left: 20px; */
  position: relative;
  /* right: 350px; */
  left: 20px;
}


.invalid-input{
  color: red;
  margin-top: -20px;
  display: block;
  /* margin-left: 20px; */
  position: relative;
  right: 90px;
  font-size: small;
}




.product-list ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.product-list ul li {
  background-color: #E36414;
  display: inline-block;
  width: 150px; /* Set the desired width */
  height: 20px; /* Set the desired height */
  padding: 10px;
  /* margin-right: 10px; Adjust as needed */
  border: 1px solid;
  text-align: center;
}

.product-list {
  font-size: medium;
  text-align: center;
}















